.header {
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__logo {
    width: 100px;
    margin-top: 18px;
    object-fit: contain;
    /* I have no idea about object-fit right now */
}

.header__search {
    display: flex;
    flex: 1;
}

.header__searchInput {
    width: 100%;
    height: 12px;
    padding: 10px;
    border: none;
}

.header__searchIcon {
    background-color: #cd9042;
    padding: 5px;
    height: 22px !important;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__link {
    color: white;
    text-decoration: none;
}

.header__option {
   display: flex;
   flex-direction: column;
   margin: 0 10px;

}

.header__optionLineOne {
    font-size: 10px;
}

.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
}

.header__basketCount {
    margin: 0 10px;
}